.form__title {
  margin-bottom: 20px;
  padding: 0;
  padding-bottom: 5px;

  font: inherit;
  font-size: 28px;
  color: inherit;

  background-color: transparent;
  border: none;
  border-bottom: 1px solid #000000;
}

.form__type {
  margin: 0;
  margin-bottom: 20px;
  padding: 0;

  border: none;
}

.form__type-list {
  display: flex;
  margin: 0;
  padding: 0;

  list-style: none;
}


.form__type-item {
  margin-right: 10px;
}

.form__type-input:checked + .form__type-label {
  background-color: rgba(0, 0, 0, 0.15);

  &:active {
    opacity: 0.5;
  }
}

.form__type-label {
  display: block;
  padding: 6px 4px;

  border-radius: 3px;
  cursor: pointer;

  transition: 0.2s ease;

  &:hover {
    background-color: rgba(0, 0, 0, 0.05);
  }
}

